import React from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import { Layout, Menu, Icon } from 'antd';

import Home from '../Home';
import Appex1 from '../Appex1';
import Appex2 from '../Appex2';

const { SubMenu } = Menu;
const {  Content, Sider, Header } = Layout;

class RouterLayout extends React.Component {
    render() {
        return(
            <Router>
                    <Layout style={{ }}>
                        <Sider width={240} style={{ paddingTop: '64px', background: '#fff' }}>
                            <div className="logo"><img src=""/></div>
                            <Menu
                                mode="inline"
                                defaultSelectedKeys={['1']}
                                style={{ height: '100%', borderRight: 0 }}
                            > 
                                
                                <Menu.Item key="1"><Link to="/">
                                    <Icon type="user" />Главная</Link>
                                    </Menu.Item>
                                <Menu.Item key="2"><Link to="/appex1">
                                    <Icon type="user" />Приложение 1</Link>
                                </Menu.Item>
                                <Menu.Item key="3">
                                    <Link to="/appex2"><Icon type="user" />Приложение 7</Link>
                                </Menu.Item>
                                <SubMenu key="sub1" title={<span><Icon type="user" />Настройки</span>}>
                                    <Menu.Item key="4"><Icon type="user" />option1</Menu.Item>
                                    <Menu.Item key="5"><Icon type="user" />option2</Menu.Item>
                                    <Menu.Item key="6"><Icon type="user" />option3</Menu.Item>
                                    <Menu.Item key="7"><Icon type="user" />option4</Menu.Item>
                                </SubMenu>
                            </Menu>
                        </Sider>
                        <Layout style={{ height: '100vh', padding: '0' }}>
                            <Header>ПП ВБД</Header>
                            <Content 
                                style={{ 
                                    background: '#f0f2f5;', 
                                    padding: 12, 
                                    margin: 12, 
                                }}
                            >
                                <div className="container">
                                    <Route exact={true} path="/" component={Home} />
                                    <Route path="/appex1" component={Appex1} />
                                    <Route path="/appex2" component={Appex2} />
                                </div>
                            </Content>
                        </Layout>
                    </Layout>
            </Router>
        );
    }
}
export default RouterLayout;


