class Appex1 extends React.Component {
    state = {
        filterDropdownVisible: false,
        data: bankData[0],
        searchText: '',
        filtered: false,
      };
    onSelectChange = (e: SelectValue, option: ReactElement<HTMLOptionElement>) => {
        console.warn(e);
        this.setState({
            data: bankData[e.toString()]
        });
    }
    /*onInputChange = (e: React.FormEvent<HTMLInputElement>) => {
        this.setState({ searchText: e.currentTarget.value });
      }
    tableSearchHandlerGenerator = (i: number) => {
        let onSearch = () => {
            const { searchText } = this.state;
            const reg = new RegExp(searchText, 'gi');
            const newData = this.state.data;
            newData.indicies[i] = newData.indicies[i].map((record) => {
                if(record){
                    const match = record.name.match(reg);
                    if (!match) {
                        return null;
                      }
                      return {
                        ...record
                      };
                }
                else return null
                
              }).filter(record => !!record)
            this.setState({
                filterDropdownVisible: false,
                filtered: !!searchText,
                data: newData,
            });
        };
        return onSearch;
    }*/
      
    render() {
        const columns: ColumnProps<Index>[] = [{
            title: 'Наименование показателя',
            dataIndex: 'name',
            key: 'name',
            width: '80%',
        }, {
            title: 'Сумма',
            dataIndex: 'sum',
            key: 'sum',
        }, ];
        return (
            <div>
            <Select defaultValue={this.state.data.orgName} style={{ width: '100%' }} onChange={this.onSelectChange}>
                {
                    bankData.map((el: Appex1Data, ind: number) => {
                        return <Option key="opt_{ind}" value={ind}> {el.orgName} </Option>;
                    })
                }
            
            </Select>
            <h1 key="name">Наименование банка: {this.state.data.orgName}</h1>
            <h3 key="okpo">Код ОКПО: {this.state.data.orgOKPO}</h3>
            <h3 key="regnum">Регистрационный номер: {this.state.data.orgRegNum}</h3>
            <Collapse accordion={true}>
                <Panel header="Размер резервируемых средств" key="1">
                    <Table columns={columns} dataSource={this.state.data.indicies[0]} />
                </Panel>
                <Panel header="Нормативная величина обязательных резервов" key="2">
                    <Table columns={columns} dataSource={this.state.data.indicies[1]} />
                </Panel>
                <Panel header="This is panel header 3" key="3">
                    <Table columns={columns} dataSource={this.state.data.indicies[2]} />
                </Panel>
                <Panel header="This is panel header 4" key="4">
                    <Table columns={columns} dataSource={this.state.data.indicies[3]} />
                </Panel>
                <Panel header="This is panel header 5" key="5">
                    <Table columns={columns} dataSource={this.state.data.indicies[4]} />
                </Panel>
            </Collapse>
            </div>
        );
      }
}

export default Appex1;


